@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  position: relative;
  min-height: 100%;
}
* {
    margin: 0;
}

body {
    font-family: 'Inter', serif;
    background-color: #fff;
}


h1 {
    font-size: 40px;
    margin: 40px;
    text-align: center;
    color: #333333;
    font-weight: bold;
}

h2 {
    font-size: 30px;
    margin: 20px;
    text-align: center;
    color: #333333;
    font-weight: bold;
}

.login {
    position: relative;
    top: 20%;
    margin: auto;
    text-align: center;
    width: 650px;
}

.login a {
    color:  #00669f;
    text-decoration: none;
}

.form_update {
    display: none;
    position: relative;
    top: 20%;
    margin: auto;
    text-align: right;
    width: 300px;
}

.form_update .entrada {
    width: 250px;
}

.entrada {
    width: 600px;
    margin: 10px;
}

.menu {
    width: 624px;
    margin: 10px;
    height: 45px;
    font-size: 15px;
    padding: 10px;
    border: 2px solid #333333;
}

.entrar {
    background-color: #2166cc;
    border-radius: 20px;
    width: 250px;
    height: 45px;
    margin: 20px;
    border: 0;
    color: #fff;
}

.entrar:hover {
    background-color: #1d519e;
}

.resetear {
    background-color: #888888;
    color: #fff;
    border-radius: 20px;
    width: 250px;
    height: 45px;
    margin: 20px;
    border: 0;
}

.resetear:hover {
    background-color: #797979;
}

input[type=text], input[type=number], input[type=password], input[type=datetime-local] {
    font-size: 15px;
    padding: 10px;
    border: 2px solid #333333;
}

input[type=button], input[type=reset] {
    font-size: 15px;
    font-weight: bold;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}


* {
    box-sizing: border-box;
    margin: 0;
}



.table-container {
    display: flex;
    width: 20%;
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: rgb(62, 111, 201);
    border-radius: 15px;
    margin: 40px auto;
    padding: 10px 15px;
}

.table-bordered {
    margin: 40px auto;
    padding: 10px 15px;
    width: 40%;
    background-color: rgb(205, 214, 231);
    border-radius: 5px;
}

.table-bordered thead {
    background-color: rgb(98, 120, 165);
}
